import React, { Component } from 'react'
import PureRenderMixin from 'react-addons-pure-render-mixin'
import Tab from '@/components/Tab'
import EmInput from '@/components/EmInput'
// import PropTypes from 'prop-types';
export default class Search extends Component {
  constructor () {
    super()
    this.shouldComponentUpdate = PureRenderMixin.shouldComponentUpdate.bind(this);
    this.state = {

    }
    this.handelSearch = this.handelSearch.bind(this)
  }
  handelSearch = (text) =>{
    console.log(text);
  }
  render () {
    return (
      <div>
        <div className="search-input">
          <div className="searchinput-box">
            <EmInput onChange={this.handelSearch} placeholder="请输入关键字" />
            <span className="search-name">搜索</span>
          </div>
        </div>
        <Tab active={0} tabs={['搜社区','搜活动']} />
        <div className="search-tabcontent">
            <div className="search-tit">热门搜索</div>
            
              {/* <ul className="search-direct hot-search">
                {
                  ['南京珠江路营业部','上海宛平南路营业部','上海宛平南路营业部','上海宛平南路营业部','上海宛平南路营业部'].map((item,index)=>{
                    return (
                      <li key={index}>
                        {index+1}.<a href="javascript:;">{item}</a>
                      </li>
                    )
                  })
                }
              </ul> */}

            <ul className="activity-search">
              {
                ['股票','基金','上海','周年庆','国企改革','国企国企改革改革'].map((item,index)=>{
                  return (
                    <li key={index}>{item}</li>
                  )
                })
              }
            </ul>
            <div className="empty-data">
              <i className="empty-search"></i>
              <div className="empty-content">暂无搜索结果</div>
            </div>
            <div className="recommand-utit">
              <span>为你推荐</span>
            </div>
            <div className="search-tit">搜索历史</div>
            <ul className="search-direct">
              <li><a className="histroy-icon" href="javascript:;">上海宛平南路营业部</a></li>
              <li><a className="histroy-icon" href="javascript:;">上海宛平南路营业部</a></li>
            </ul>
            <div className="clear-history">清空历史记录</div>
          </div>
      </div>
    )
  }
}
